import {
  Component,
  SimpleChanges, Input, ContentChild,
  OnChanges,
  ChangeDetectionStrategy,
  TemplateRef,
} from '@angular/core';

@Component({
  // tslint:disable-next-line:component-selector
  selector: '[semui-font-sizes]',
  template: `<div class="sem-ul-icon-list sem-ul">
  <ng-content></ng-content>
</div>



<ul class="bullet-inlinelist p4">
  <li class="active">
    <div class="select">
      <select type="number" [(ngModel)]="selectedLevel">
        <option *ngFor="let level of levels" [ngValue]="level">{{level.name}}</option>
      </select>
    </div>
  </li>
  <li>
    <div class="select">
      <select type="number" [(ngModel)]="selectedLevel">
        <option *ngFor="let level of levels" [ngValue]="level">{{level.name}}</option>
      </select>
    </div>
  </li>
  <li>
    <div class="select">
      <select type="number" [(ngModel)]="selectedLevel">
        <option *ngFor="let level of levels" [ngValue]="level">{{level.name}}</option>
      </select>
    </div>
  </li>
  <li>
    <div class="select">
      <select type="number" [(ngModel)]="selectedLevel">
        <option *ngFor="let level of levels" [ngValue]="level">{{level.name}}</option>
      </select>
    </div>
  </li>
</ul>
`,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SemUiFontSizesComponent implements OnChanges {
  @Input() activeid: number;

  @ContentChild(TemplateRef)
  public template: TemplateRef<any>;

  @Input()
  public dataSource: any = null;

  @Input()
  public selectedItem: any = {};

  @Input()
  public openedParent: any = null;


  constructor() { }

  levels: Array<Object> = [
    {num: 0, name: '1'},
    {num: 1, name: '2'}
  ];
  selectedLevel = this.levels[0];
  ngOnChanges(changes: SimpleChanges): void {
    console.log('changes are enabled')
    this.activeid = changes.activeid.currentValue;
  }

}
