import {
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  OnInit,
  ViewEncapsulation,
} from '@angular/core';

export type  HeaderImportanceLevel = '' | undefined;

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  // tslint:disable-next-line:component-selector
  selector: 'div[semui-section-header]',
  template: `
    <ng-content></ng-content>
  `
})
export class SectionHeaderComponent implements OnInit {
  /**
   * Css base class.
   */
  @HostBinding('class.semui-header-section')
  public addBaseCssClass = true;

  /**
   * Bind to the host "small" CSS class.
   */
  @HostBinding('class.small')
  public styleAsSmall: boolean;

  /**
   * Button importance level in terms of primary/secondary/etc.
   */
  @Input('semui-importance')
  public semuiImportance: HeaderImportanceLevel;

  /**
   * Bind to the host "large" CSS class.
   */
  @HostBinding('class.large')
  public styleAsLarge: boolean;

  constructor() { }

  ngOnInit() {
    this.styleElementByImportanceLevel(this.semuiImportance);
  }
  /**
   * Set component to be on dark or light.
   */
  private styleElementByImportanceLevel(labelType) {
    switch (labelType) {
      case ('small'):
        this.styleAsSmall = true;
        break;
      case ('large'):
        this.styleAsLarge = true;
        break;
      default:
        break;
    }
  }
}
