import {
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  OnInit,
  ViewEncapsulation,
} from '@angular/core';

/**
 * Possible button importance levels.
 */
export type LabelImportanceLevel = 'small' | 'large' | undefined;
export type LabelTheme = 'light' | 'dark' | undefined;


@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  // tslint:disable-next-line:component-selector
  selector: 'label[semui-label]',
  template: `
    <ng-content></ng-content>
  `
})
export class LabelComponent implements OnInit {
  /**
   * Css base class.
   */
  @HostBinding('class.semui-label')
  public addBaseCssClass = true;

  /**
   * Bind to the host "small" CSS class.
   */
  @HostBinding('class.small')
  public styleAsSmall: boolean;

  /**
   * Bind to the host "medium" CSS class.
   */
  @HostBinding('class.medium')
  public styleAsMedium: boolean;


  /**
   * Bind to the host "large" CSS class.
   */
  @HostBinding('class.large')
  public styleAsLarge: boolean;


  /**
   * Bind to the host "large" CSS class.
   */
  @HostBinding('class.form-label')
  public styleAsFormLabel: boolean;

  /**
   * Button importance level in terms of primary/secondary/etc.
   */
  @Input('semui-importance')
  public semuiImportance: LabelImportanceLevel;


  /**
   * Bind to the host "large" CSS class.
   */
  @HostBinding('class.dark')
  public styleAsDark: boolean;

  /**
   * Bind to the host "large" CSS class.
   */
  @HostBinding('class.light')
  public styleAsLight: boolean;


  /**
   * Label theme in terms of light/dark.
   */
  @Input('semui-theme')
  public semuiTheme: LabelTheme;

  ngOnInit() {
    this.styleElementByImportanceLevel(this.semuiImportance);
    this.setTheme(this.semuiTheme);
  }
  /**
   * Set component to be on dark or light.
   */
  private setTheme(type) {
    switch (type) {
      case ('light'):
        this.styleAsDark = true;
        break;
      case ('dark'):
        this.styleAsLight = true;
        break;
      default:
    }
  }
  /**
   * Set component's properties bound to
   * the host element CSS classes.
   */
  private styleElementByImportanceLevel(labelType) {

    switch (labelType) {
      case ('small'):
        this.styleAsSmall = true;
        break;
      case ('medium'):
        this.styleAsMedium = true;
        break;
      case ('large'):
        this.styleAsLarge = true;
        break;
      case ('form-label'):
        this.styleAsFormLabel = true;
      default:
    }
  }
}
