import {
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  OnInit,
  ViewEncapsulation,
} from '@angular/core';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  // tslint:disable-next-line:component-selector
  selector: 'option[semui-form-options]',
  template: `<ng-content></ng-content>`,
})
export class FormOptionsComponent implements OnInit {
  /**
   * Always apply the base Rubix button css class.
   */
  @HostBinding('class.semui-option')
  public addBaseCssClass: true;


  constructor() { }

  ngOnInit() {
  }

}
