import {
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  HostListener,
  Input,
  OnInit,
  ViewEncapsulation,
} from '@angular/core';

/**
 * Possible button importance levels.
 */

export type ThemeImportanceLevel = 'light' | 'dark' | undefined;

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  // tslint:disable-next-line:component-selector
  selector: 'button[sem-button], div[sem-button]',
  template: `
    <ng-content></ng-content>
  `
})
export class ButtonComponent implements OnInit {
  /**
   * Bind to the host "dark" CSS class.
   */
  @HostBinding('class.active')
  public styleAsActive: boolean = false;


  @HostListener('click', ['$event.target']) onClick(btn) {
    this.styleAsActive = !this.styleAsActive;
  }

  /**
   * Bind to the host "light" CSS class.
   */
  @HostBinding('class.light')
  public styleAsLightTheme: boolean;

  /**
   * Bind to the host "dark" CSS class.
   */
  @HostBinding('class.dark')
  public styleAsDarkTheme: boolean;


  /**
   * Button importance level in terms of primary/secondary/etc.
   */
  @Input('sem-theme')
  public semuiTheme: ThemeImportanceLevel;

  @Input('large') large: string | undefined;
  @Input('small') small: string | undefined;
  @Input('full') full: string | undefined;
  @Input('block') block: string | undefined;
  @Input('linkbutton') linkbutton: string | undefined;
  @Input('disabled') disabled: string | undefined;
  @Input('icononly') icononly: string | undefined;
  @Input('sem-importance') semImportance: string | undefined;
  @HostBinding('class') @Input('class') classes: string | undefined;



  ngOnInit() {
    this.addClasses( this.getClasses() )
  }

  getClasses(){
    const _classes = [];
    _classes.push(' sem-button');
    if (this.block !== undefined) {
      _classes.push('block');
    }
    if (this.full !== undefined) {
      _classes.push('full');
    }
    if (this.large !== undefined) {
      _classes.push('large');
    }
    if (this.small !== undefined) {
      _classes.push('small');
    }
    if (this.disabled !== undefined) {
      _classes.push('disabled');
    }

    if (this.icononly !== undefined) {
      _classes.push('icononly');
    }
    if (this.semImportance === 'primary') {
      _classes.push('primary');
    }
    if (this.semImportance === 'link_primary') {
      _classes.push('link_primary');
    }
    if (this.linkbutton !== undefined) {
      _classes.push('link');
    }
    if (this.semImportance === 'secondary') {
      _classes.push('secondary');
    }
    return _classes;
  }

  addClasses(classes: string[]) {
    const spacer = this.classes ? '' : '';
    const base = 'sem-button--';
    classes.forEach((_class, index) => {
      if (index === 0) {
        this.classes += ' ' + spacer + base + _class;
      } else {
        this.classes += ' ' + base + _class;
      }
    });
  }




}
