import {
  Component, OnInit, ChangeDetectionStrategy,
  Input, ViewEncapsulation, HostBinding
} from '@angular/core';
import { ListItemType } from '../../../models/list-type';

/**
 * Possible button importance levels.
 */
export type ThemeImportanceLevel =
    'light'
  | 'dark'
  | undefined;

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  // tslint:disable-next-line:component-selector
  selector: '[semui-list-item]',
  template: `<ng-content></ng-content>
`
})
export class ListItemComponent implements OnInit {
  @Input() item: ListItemType;
  @Input('sem-importance') semImportance: string;
  @HostBinding('class') @Input('class') classes: string;
  @Input('sem-theme')
  public semuiTheme: ThemeImportanceLevel;

  /**
   * Bind to the host "light" CSS class.
   */
  @HostBinding('class.sem-li-light')
  public styleAsLightTheme: boolean;

  /**
   * Bind to the host "dark" CSS class.
   */
  @HostBinding('class.sem-li-dark')
  public styleAsDarkTheme: boolean;


  ngOnInit() {
    this.addClasses( this.getClasses() )
  }

  addClasses(classes: string[]) {
    const spacer = this.classes ? '' : '';
    const base = ' sem-li--';
    classes.forEach((_class, index) => {
      if (index === 0) {
        this.classes += ' ' + spacer + base + _class;
      } else {
        this.classes += ' ' + base + _class;
      }
    });
  }

  getClasses() {
    const _classes = [];
    _classes.push(' sem-li');
    if (this.semImportance === 'default') {
      _classes.push('default');
    }
    if (this.semImportance === 'inverted') {
      _classes.push('inverted');
    }
    if (this.semImportance === 'primary') {
      _classes.push('primary');
    }
    if (this.semImportance === 'secondary') {
      _classes.push('secondary');
    }
    if (this.semImportance === 'tertiary') {
      _classes.push('tertiary');
    }
    if (this.semImportance === 'danger') {
      _classes.push('danger');
    }
    return _classes;
  }

}
